import React from "react";
import { render } from "react-dom";
import { Rnd } from "react-rnd";

const style = {
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  border: "solid 1px #ddd",
  background: "#f0f0f0"
};

class RndElement extends React.Component {
  constructor() {
    super();
    this.state = {
      width: 150,
      height: 150,
      x: 130,
      y: 10*75
    };
  }



  render() {
    return (
      <Rnd
        style={style}
        size={{ width: this.state.width, height: this.state.height }}
        position={{ x: this.state.x, y: this.state.y }}
        onDragStop={(e, d) => {
          this.setState({ x: d.x, y: d.y });
        }}
        onResizeStop={(e, direction, ref, delta, position) => {
          this.setState({
            width: ref.style.width,
            height: ref.style.height,
            ...position
          });
        }}
        bounds="window"
        minWidth={150}
        minHeight={150}aaaaaaaaaaaaaaaaaaaaa
      >
      <div>
        <img style={{height: this.state.height,width: this.state.width }}  src={"https://spencermensah.github.io/img/" + this.props.index + ".png"}></img>
      </div>
      </Rnd>
    );
  }
}

export default RndElement;
